import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const AnimatedBackground = () => {
  const [windowSize, setWindowSize] = useState({
    width: typeof window !== 'undefined' ? window.innerWidth : 1920,
    height: typeof window !== 'undefined' ? window.innerHeight : 1080
  });
  
  const [isMobile, setIsMobile] = useState(false);
  const [shouldAnimate, setShouldAnimate] = useState(true);

  useEffect(() => {
    const handleResize = () => {
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight
      });
      setIsMobile(window.innerWidth < 768);
    };

    // Check for reduced motion preference
    const mediaQuery = window.matchMedia('(prefers-reduced-motion: reduce)');
    setShouldAnimate(!mediaQuery.matches);

    handleResize();
    window.addEventListener('resize', handleResize);
    
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Reduce particle count on mobile for better performance
  const particleCount = isMobile ? 8 : 16;
  const orbCount = isMobile ? 3 : 5;
  
  // Adjust animation durations for mobile
  const animationSpeeds = {
    orb1: isMobile ? 35 : 25,
    orb2: isMobile ? 40 : 30,
    orb3: isMobile ? 32 : 22,
    orb4: isMobile ? 45 : 35,
    orb5: isMobile ? 38 : 28,
    grid: isMobile ? 40 : 30,
    pulse: isMobile ? 15 : 10,
  };

  if (!shouldAnimate) {
    return (
      <div className="fixed inset-0 pointer-events-none z-[-10]" aria-hidden="true">
        <div 
          className="absolute inset-0 opacity-30"
          style={{
            background: 'radial-gradient(ellipse at center, rgba(59, 130, 246, 0.12) 0%, transparent 70%)'
          }}
        />
      </div>
    );
  }

  return (
    <div className="fixed inset-0 pointer-events-none z-[-10]" aria-hidden="true">
      {/* Enhanced gradient background with multiple layers */}
      <motion.div 
        className="absolute top-0 left-0 w-full h-full overflow-hidden"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 3 }}
      >
        {/* Primary large floating orb with enhanced gradients */}
        <motion.div
          className={`absolute ${isMobile ? 'w-80 h-80' : 'w-[500px] h-[500px]'} rounded-full`}
          style={{
            background: `
              radial-gradient(circle at 30% 30%, rgba(59, 130, 246, 0.25) 0%, rgba(99, 179, 237, 0.15) 30%, transparent 70%),
              radial-gradient(circle at 70% 70%, rgba(147, 197, 253, 0.2) 0%, transparent 50%)
            `,
            filter: isMobile ? 'blur(30px)' : 'blur(60px)',
            willChange: 'transform',
            opacity: 0.8,
          }}
          initial={{ x: -300, y: -300, scale: 0.5 }}
          animate={{ 
            x: [-300, 200, -100, -300],
            y: [-300, 100, 400, -300],
            scale: [0.5, 1.2, 0.8, 0.5],
            rotate: [0, 360, 720, 1080],
          }}
          transition={{
            duration: animationSpeeds.orb1,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />

        {/* Secondary orb with different color palette */}
        <motion.div
          className={`absolute ${isMobile ? 'w-64 h-64' : 'w-96 h-96'} rounded-full`}
          style={{
            background: `
              radial-gradient(circle at 40% 60%, rgba(30, 64, 175, 0.3) 0%, rgba(59, 130, 246, 0.2) 40%, transparent 70%),
              radial-gradient(circle at 60% 40%, rgba(37, 99, 235, 0.25) 0%, transparent 60%)
            `,
            filter: isMobile ? 'blur(25px)' : 'blur(50px)',
            willChange: 'transform',
            opacity: 0.7,
          }}
          initial={{ x: windowSize.width + 200, y: windowSize.height + 200, scale: 0.3 }}
          animate={{ 
            x: [windowSize.width + 200, windowSize.width - 400, windowSize.width - 100, windowSize.width + 200],
            y: [windowSize.height + 200, windowSize.height - 300, windowSize.height - 500, windowSize.height + 200],
            scale: [0.3, 1, 0.6, 0.3],
            rotate: [0, -360, -720, -1080],
          }}
          transition={{
            duration: animationSpeeds.orb2,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />

        {/* Third orb with cool blue tones */}
        <motion.div
          className={`absolute ${isMobile ? 'w-72 h-72' : 'w-[400px] h-[400px]'} rounded-full`}
          style={{
            background: `
              radial-gradient(circle at 50% 20%, rgba(125, 211, 252, 0.2) 0%, rgba(59, 130, 246, 0.15) 50%, transparent 80%),
              radial-gradient(circle at 80% 80%, rgba(56, 189, 248, 0.18) 0%, transparent 60%)
            `,
            filter: isMobile ? 'blur(20px)' : 'blur(45px)',
            willChange: 'transform',
            opacity: 0.6,
          }}
          initial={{ x: windowSize.width / 2, y: -200, scale: 0.4 }}
          animate={{ 
            x: [windowSize.width / 2, windowSize.width / 2 + 300, windowSize.width / 2 - 200, windowSize.width / 2],
            y: [-200, windowSize.height / 2, windowSize.height + 100, -200],
            scale: [0.4, 1.1, 0.7, 0.4],
            rotate: [0, 180, 360, 540],
          }}
          transition={{
            duration: animationSpeeds.orb3,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />

        {/* Fourth orb - only on desktop */}
        {!isMobile && (
          <motion.div
            className="absolute w-80 h-80 rounded-full"
            style={{
              background: `
                radial-gradient(circle at 25% 75%, rgba(14, 165, 233, 0.22) 0%, rgba(59, 130, 246, 0.12) 45%, transparent 75%),
                radial-gradient(circle at 75% 25%, rgba(37, 99, 235, 0.18) 0%, transparent 55%)
              `,
              filter: 'blur(35px)',
              willChange: 'transform',
              opacity: 0.5,
            }}
            initial={{ x: windowSize.width * 0.8, y: windowSize.height * 0.8, scale: 0.2 }}
            animate={{ 
              x: [windowSize.width * 0.8, windowSize.width * 0.2, windowSize.width * 0.9, windowSize.width * 0.8],
              y: [windowSize.height * 0.8, windowSize.height * 0.3, windowSize.height * 0.1, windowSize.height * 0.8],
              scale: [0.2, 0.9, 0.5, 0.2],
              rotate: [0, 270, 540, 810],
            }}
            transition={{
              duration: animationSpeeds.orb4,
              repeat: Infinity,
              ease: "easeInOut"
            }}
          />
        )}

        {/* Fifth orb - desktop only */}
        {!isMobile && (
          <motion.div
            className="absolute w-60 h-60 rounded-full"
            style={{
              background: `
                radial-gradient(circle at 60% 40%, rgba(30, 58, 138, 0.25) 0%, rgba(59, 130, 246, 0.15) 50%, transparent 80%),
                radial-gradient(circle at 40% 60%, rgba(29, 78, 216, 0.2) 0%, transparent 65%)
              `,
              filter: 'blur(28px)',
              willChange: 'transform',
              opacity: 0.4,
            }}
            initial={{ x: windowSize.width * 0.1, y: windowSize.height * 0.5, scale: 0.6 }}
            animate={{ 
              x: [windowSize.width * 0.1, windowSize.width * 0.7, windowSize.width * 0.3, windowSize.width * 0.1],
              y: [windowSize.height * 0.5, windowSize.height * 0.1, windowSize.height * 0.9, windowSize.height * 0.5],
              scale: [0.6, 1.3, 0.8, 0.6],
              rotate: [0, -180, -360, -540],
            }}
            transition={{
              duration: animationSpeeds.orb5,
              repeat: Infinity,
              ease: "easeInOut"
            }}
          />
        )}

        {/* Enhanced animated particles with varied sizes and colors */}
        {[...Array(particleCount)].map((_, i) => (
          <motion.div
            key={i}
            className={`absolute rounded-full ${
              i % 3 === 0 ? 'w-3 h-3' : i % 3 === 1 ? 'w-2 h-2' : 'w-1 h-1'
            }`}
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              background: i % 4 === 0 
                ? 'radial-gradient(circle, rgba(59, 130, 246, 0.6) 0%, transparent 70%)'
                : i % 4 === 1
                ? 'radial-gradient(circle, rgba(147, 197, 253, 0.5) 0%, transparent 70%)'
                : i % 4 === 2
                ? 'radial-gradient(circle, rgba(125, 211, 252, 0.7) 0%, transparent 70%)'
                : 'radial-gradient(circle, rgba(56, 189, 248, 0.4) 0%, transparent 70%)',
              willChange: 'transform, opacity',
              filter: 'blur(1px)',
            }}
            animate={{
              y: [0, isMobile ? -40 : -60, 0],
              x: [0, Math.random() * 20 - 10, 0],
              opacity: [0.1, 0.9, 0.1],
              scale: [0.5, 2, 0.5],
              rotate: [0, 360, 720],
            }}
            transition={{
              duration: 4 + Math.random() * 6,
              repeat: Infinity,
              delay: Math.random() * 4,
              ease: "easeInOut"
            }}
          />
        ))}

        {/* Floating light streaks */}
        {!isMobile && [...Array(6)].map((_, i) => (
          <motion.div
            key={`streak-${i}`}
            className="absolute w-1 h-20 rounded-full"
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              background: `linear-gradient(to bottom, 
                rgba(59, 130, 246, 0.8) 0%, 
                rgba(147, 197, 253, 0.6) 50%, 
                transparent 100%)`,
              transform: `rotate(${Math.random() * 360}deg)`,
              willChange: 'transform, opacity',
              filter: 'blur(2px)',
            }}
            animate={{
              opacity: [0, 0.8, 0],
              scale: [0.5, 1.5, 0.5],
              rotate: [0, 180, 360],
            }}
            transition={{
              duration: 3 + Math.random() * 4,
              repeat: Infinity,
              delay: Math.random() * 3,
              ease: "easeInOut"
            }}
          />
        ))}
      </motion.div>

      {/* Enhanced animated grid overlay with depth */}
      <motion.div
        className="absolute inset-0 opacity-15"
        style={{
          backgroundImage: `
            linear-gradient(rgba(59, 130, 246, 0.08) 1px, transparent 1px),
            linear-gradient(90deg, rgba(59, 130, 246, 0.08) 1px, transparent 1px),
            linear-gradient(rgba(147, 197, 253, 0.04) 1px, transparent 1px),
            linear-gradient(90deg, rgba(147, 197, 253, 0.04) 1px, transparent 1px)
          `,
          backgroundSize: isMobile 
            ? '40px 40px, 40px 40px, 80px 80px, 80px 80px' 
            : '60px 60px, 60px 60px, 120px 120px, 120px 120px',
          willChange: 'background-position',
        }}
        animate={{
          backgroundPosition: isMobile 
            ? ['0px 0px, 0px 0px, 0px 0px, 0px 0px', '40px 40px, 40px 40px, 80px 80px, 80px 80px']
            : ['0px 0px, 0px 0px, 0px 0px, 0px 0px', '60px 60px, 60px 60px, 120px 120px, 120px 120px'],
        }}
        transition={{
          duration: animationSpeeds.grid,
          repeat: Infinity,
          ease: "linear"
        }}
      />

      {/* Multi-layered pulsing gradient with enhanced colors */}
      <motion.div
        className="absolute inset-0"
        style={{
          background: `
            radial-gradient(ellipse at 30% 70%, rgba(59, 130, 246, 0.05) 0%, transparent 60%),
            radial-gradient(ellipse at 70% 30%, rgba(147, 197, 253, 0.04) 0%, transparent 50%),
            radial-gradient(circle at 50% 50%, rgba(125, 211, 252, 0.03) 0%, transparent 70%)
          `,
          willChange: 'transform, opacity',
        }}
        animate={{
          scale: [1, 1.2, 1],
          opacity: [0.4, 0.8, 0.4],
          rotate: [0, 5, 0],
        }}
        transition={{
          duration: animationSpeeds.pulse,
          repeat: Infinity,
          ease: "easeInOut"
        }}
      />

      {/* Ambient light effects */}
      <motion.div
        className="absolute inset-0"
        style={{
          background: `
            conic-gradient(from 0deg at 20% 20%, 
              transparent 0deg, 
              rgba(59, 130, 246, 0.02) 90deg, 
              transparent 180deg),
            conic-gradient(from 180deg at 80% 80%, 
              transparent 0deg, 
              rgba(147, 197, 253, 0.03) 90deg, 
              transparent 180deg)
          `,
          willChange: 'transform',
        }}
        animate={{
          rotate: [0, 360],
        }}
        transition={{
          duration: 50,
          repeat: Infinity,
          ease: "linear"
        }}
      />
    </div>
  );
};

export default AnimatedBackground;
